import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, CheckCircle, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { useHistory } from 'react-router-dom'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import CommonDataTable from '../../CommonDataTable'
import EditModal from './EditModal'

const AllUsers = () => {
    const history = useHistory()
    const parent_ID = JSON.parse(localStorage.getItem('userData')).user_id
    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [managerList, setmanagerList] = useState([])
    const [ManagerInfo, setManagerInfo] = useState({})

    const [modal, setModal] = useState(false)
    const toggleModal = () => setModal(!modal)

    useEffect(() => {
        useJwt.SuperAdminUsersList().then(res => {
            console.log(res.data.payload)
            setmanagerList(res.data.payload.length ? res.data.payload.filter(x => x.user_id !== parent_ID) : [])
            // setmanagerList(res.data.payload)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err)
            setTableDataLoading(false)
        })
    }, [resetData])

    const handleDelete = (super_admin_user_id, message) => {
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                // console.log({ land_lord_id })
                return useJwt.deleteSuperAdminUser({ super_admin_user_id }).then(res => {
                    console.log(res)
                    setmanagerList(managerList.filter(x => x.user_id !== super_admin_user_id))
                }).catch(err => {
                    Error(err)
                    console.log(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {
            }
        })
    }
    const column = [
        {
            name: 'Photo',
            maxWidth: '100px',
            // sortable: true,
            selector: row => <img src={row.photo} alt="photo" width="50px" />
        },
        {
            name: 'Name',
            minWidth: '100px',
            sortable: true,
            selector: 'full_name'
        },
        {
            name: 'Email',
            minWidth: '100px',
            sortable: true,
            selector: 'email'
        },
        {
            name: 'Mobile',
            minWidth: '100px',
            sortable: true,
            selector: 'mobile'
        },
        {
            name: 'Address',
            minWidth: '100px',
            // sortable: true,
            selector: 'address'
        },
        {
            name: 'Action',
            minWidth: '100px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Edit">
                        <Edit size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('UserEditInfo', JSON.stringify(row))
                                history.push('/SuperAdmin/EditUser')
                                // setManagerInfo(row)
                                // setModal(true)
                            }}
                        />
                    </span>&nbsp;&nbsp;
                    <Trash size={15}
                        color='crimson'
                        style={{ cursor: 'pointer' }}
                        onClick={(e) => handleDelete(row.user_id, "Do you want to delete?")}
                    />
                </>
            }
        }
    ]
    return (
        <Card>
            <CardHeader className='border-bottom'>
                <CardTitle tag='h4'>My Users List</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col md='12'>
                        <CommonDataTable column={column} TableData={managerList} TableDataLoading={TableDataLoading} />
                    </Col>
                </Row>
                <EditModal
                    toggleModal={toggleModal}
                    modal={modal}
                    resetData={resetData}
                    setReset={setReset}
                    setManagerInfo={setManagerInfo}
                    ManagerInfo={ManagerInfo}
                />
            </CardBody>
        </Card>
    )
}

export default AllUsers